import { JsonML, getTagName, getAttributes } from '@ali/4ever-utils';
import { Container } from '../models';

export default {
  name: 'container',

  match(node: JsonML) {
    return (
      getTagName(node) === Container.TYPE
    );
  },

  convert(node: JsonML) {
    const data = { ...getAttributes(node) } as any;
    return Container.createContainer({
      data,
    });
  },
};
